import styled from 'styled-components'

const Header = styled.header`
  width: 100%;
  height: 74px;
  background: #fff;
  box-shadow: ${props => props.theme.light.shadows.primary};
  grid-area: header;
  display: flex;
  align-items: center;
`

export default Header



