import React, { Suspense, lazy } from 'react'
import { Route, Switch, Redirect } from 'react-router-dom'
import Loading from '../../../components/Loading'
import MainContainer from '../../../modules/Core/Main'
import NotFound from '../../../modules/Core/NotFound'

const Register = lazy(() => import('../../../modules/Task/Register'))
const Task = lazy(() => import('../../../modules/Task'))
const Report = lazy(() => import('../../../modules/Task/End'))

const loading = () => <Loading />

const PrivateRouteComponent = () => {
  return (
    <Suspense fallback={loading()}>
      <Switch>
        <Redirect exact from='/' to='/register' />
        <MainContainer>
          <Switch>
            <Route path='/register' name='Form Task' render={props => (<Register {...props} />)} />
            <Route path='/task' name='Task' render={props => (<Task {...props} />)} />
            <Route path='/report' name='Report' render={props => (<Report {...props} />)} />
          </Switch>
        </MainContainer>
        <Route component={NotFound} />
      </Switch>
    </Suspense>
  )
}

export default PrivateRouteComponent



