import React from 'react'
import styled from 'styled-components'
const Loader = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;

  &.default,
  &.context {
    top:0;
    left:0;
    width:100%;
    height:100%;
    z-index: 9;
  }

  &.default {
    position: fixed;
    background: rgba(255, 255, 255, .8);
  }

  &.context {
    position: absolute;
    background: rgba(255, 255, 255, .6);
  }


  #load {
    width: 60px;
    animation: loading 3s linear infinite;

    &.small {
      width: 30px
    }
  }

  #load #loading-inner {
    stroke-dashoffset: 0;
    stroke-dasharray: 300;
    stroke-width: 10;
    stroke-miterlimit: 10;
    stroke-linecap: round;
    animation: loading-circle 2s linear infinite;
    stroke: ${props => props.theme.light.primary.default};
    fill: transparent;
  }
  
  @keyframes loading {
    0% {
      transform: rotate(0);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  @keyframes loading-circle {
    0% {
      stroke-dashoffset: 0;
    }
    100% {
      stroke-dashoffset: -600;
    }
  }
  
`
const Loading = ({
  size = 'normal',
  overflow = 'default'
}) => (
  <Loader className={overflow}>
    <svg id="load" className={size} x="0px" y="0px" viewBox="0 0 150 150">
      <circle id="loading-inner" cx="75" cy="75" r="60"/>
    </svg>
  </Loader>
)

export default Loading



