const setColor = (
  item,
  darker,
  lighter,
  with100,
  with200
) => ({
  default: item,
  darker,
  lighter,
  with100,
  with200
})

export const theme = {
  common: {
    fontFamily:
      "'Poppins', Roboto, 'Helvetica Neue', sans-serif"
  },
  light: {
    type: 'light',
    bgColor: '#F7F7F7',
    primary: setColor('#0869AC', '#091C5E', '#0B67C1', '#0E7BC6', '#AFD9D6'),
    neutral: setColor('#6E767A', '#454F55', '#979DA0', '#E8E8E8', '#F5F5F5'),
    
    semantic: {
      alert: setColor('#FFC544', '#CC9312', '#FFD882', '#FFF2D5', null),
      error: setColor('#EE6262', '#CC1212', '#FF8282', '#FFD5D5', null),
      success: setColor('#33CFB1', '#00896F', '#6EE2B5', '#C7F7DF', null),
      info: setColor('#2090EB', '#1284CC', '#82E7FF', '#D5F9FF')
    },

    shadows: {
      primary: '0px 6px 14px rgba(0, 0, 0, .1)',
      secondary: '0px 4px 6px rgba(0, 0, 0, .16)'
    },

    buttons: {
      sizes: {
        larger: '45',
        medium: '35',
        small: '24'
      }
    }
  }
}



